body {
  font-family: $primaryfont;
  font-size: 1.3em;
  line-height: $lineheight;

  @media screen and (min-width: $iphone7) and (max-width: $desktop) {
    font-size: 1.7em;
  }
}


h1 {
  font-family: $headerfont;
  margin: 0;
  letter-spacing: $spacing;
  text-align: center;
  line-height: $lineheight;
}

h2 {
  font-family: $headerfont2;
  text-transform: uppercase;
  letter-spacing: $spacing;
  text-align: center;
  line-height: $lineheight;
  font-size: 2em;

  @media screen and (min-width: $iphone7) and (max-width: $desktop) {
    font-size: 2em;
  }
}

h3 {
  font-family: $headerfont2;
  text-transform: uppercase;
  letter-spacing: $spacing;
  text-align: center;
  line-height: $lineheight;
  font-size: 1.6em;
}

.headerline {
  border-left: 3px solid $yellow;
  text-align: left;
  padding: 0 15px;
}

p {
  padding: 0 18px;
}

ul {
  margin: 0;
  padding: 0;
}

ol {
  padding-left: 26px;
  padding-right: 18px;
  margin: 0;
  li {
    margin: 0.5em;
  }
}

a {
  text-decoration: none;
  color: $yellow;
  transition: $transnorm;

  &:hover {
    color: darken($yellow, 15%);
  }
}

.colored-list {
  color: $red;
  list-style: square;
  padding: 0 36px;
  li {
    margin: ($lineheight / 2) 0;
  }
  span {
    color: $white;
  }
}

.text-large-bold {
  font-size: $fontLarge;
  font-weight: 700;
}

#baguetteBox-overlay .full-image figcaption {
  font-family: $primaryfont;
}

/*--------------------------------------
    JUSTIFICATION - 'Cause it's pretty
---------------------------------------*/
@supports (hyphens:auto) {
  @media screen and (min-width: $iphoneSE) {
    p {
      hyphens: auto;
      text-align: justify;
    }
    .text-align-center {
      text-align: center;
    }
  }
}

/*--------------------------------------
 TYPEKIT FLASH FIX/UNSUPPORTED WEB FONTS
---------------------------------------*/
.wf-loading {
  /* styles to use when web fonts are loading */
  body {
    display: none;
  }
}

.wf-active {
/* styles to use when web fonts are active */
}

.wf-inactive {
/* styles to use when web fonts are inactive */
}


/*--------------------------------------
      TAMING OF THE FONT AWESOME
---------------------------------------*/
.fa {
  display: flex;
  align-items: center;
  justify-content: center;
  &::before {
    display: block;
  }
}
