.slider {
  width: 100%;
  height: 100%;
}
.skin-default {
  width: 100%;
  height: 100%;
  position: relative;

  .views_slideshow_cycle_main {
    width: 100%;
    height: 100%;
  }

  .views_slideshow_cycle_teaser_section, .views_slideshow_slide {
    width: 100% !important;
    height: 100% !important;
  }

  .views_slideshow_cycle_teaser_section {
    overflow: hidden;
  }


  .slide {
    width: 100%;
    height: 100%;

    img {
      width: auto;
      height: 100%;
      position: absolute;

      @supports (transform: translate(-50%, -50%)) or (-ms-transform: translate(-50%, -50%)) or (-webkit-transform: translate(-50%, -50%)) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

      @media screen and (orientation: landscape) {
        width: auto;
        height: 100%;
      }

      // @media screen and (min-width: $desktop) {
      //   width: 100%;
      //   height: auto;
      // }
    }
  }

  .views-slideshow-controls-bottom {
    position: absolute;
    left: 0;
    top: calc(50% - 1.5em);
    z-index: 100;
    width: 100%;


    .views_slideshow_controls_text {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-size: 3em;
      padding: 0 $padding;

      a {
        color: $white;
        text-shadow: $shadow;

        &:hover {
          color: $yellow;
        }
      }
    }

    .views_slideshow_controls_text_pause {
      display: none;
    }
  }
}
