/*--------------------------------------
              TWO-COLUMN
---------------------------------------*/
#column-page-layout {
  padding: $padding ($padding / 2);
  @media screen and (min-width: $desktop) {
    padding: ($padding * 2) $padding;
  }
  .column-layout {
    width: 100%;
    padding: ($padding / 4) ($padding / 2) ;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
      display: flex;
    }

    .block-region-column-left {
      width: 100%;
      @media screen and (min-width: $desktop) {
        width: 50%;
        padding-right: ($padding / 2);
      }
    }
    .block-region-column-right {
      width: 100%;
      @media screen and (min-width: $desktop) {
        width: 50%;
        padding-left: ($padding / 2);
      }
    }
  }

  /*--------------------------------------
              TOP COLUMN 33/66%
  ---------------------------------------*/
  .column-layout-33-66 {
    width: 100%;
    padding: ($padding / 4) 0;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
      display: flex;
    }

    .block-region-top-left {
      @media screen and (min-width: $desktop) {
        max-width: 33.333333%;
        padding-right: $padding / 4;
      }
      h1 { //Same as .headerline in Typography
        border-left: 3px solid $yellow;
        text-align: left;
        padding: 0 15px;
        margin:  ($padding/2) 0;
        @media screen and (min-width: $desktop) {
          padding: 0 15px;
          margin-top: 0;
        }
      }

      img {
        width: 100%;
        height: auto;
      }
    }
    .block-region-top-right {
      @media screen and (min-width: $desktop) {
        max-width: 66.666666%;
        padding-left: $padding / 4;
        display: flex;
        flex-direction: column;
      }

      h1 { //Same as .headerline in Typography
        border-left: 3px solid $yellow;
        text-align: left;
        padding: 0 15px;
        margin:  ($padding/2) 0;
        @media screen and (min-width: $desktop) {
          padding: 0 15px;
          margin-top: 0;
        }
      }

      img {
        width: 100%;
        height: auto;
      }
    }
  }

  .block-region-middle, .block-region-bottom {
    padding: ($padding / 4) ($padding / 2) ;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
    }
  }
}
