#homepage-layout {
  width: 100%;

  .scroll-down {
    width: 100%;
    position: absolute;
    bottom: 5%;
    left: 0;
    @media screen and (orientation: landscape) {
      width: 100%;
      span {
        content: "test";
      }
    }
  }

  /*--------------------------------------
                FIRST BLOCK
  ---------------------------------------*/
  .first-block {
    width: 100%;
    height: auto;
    min-height: calc(100vh - $menuHeight);
    position: relative;

    .home-image {
      width: 100%;
      height: 100vh;
      overflow: hidden;

      div {
        width: 100%;
        height: 100%;
      }

      img {
        width: auto;
        height: 100%;
        @media screen and (orientation: landscape) {
          width: 100%;
          height: auto;
        }
      }
    }

    .first-block-overlay {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      @media screen and (min-width: $desktop) {
        padding: 0 $padding;
      }

      .overlay-container {
        position: relative;
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @media screen and (min-width: $desktop) {
          flex-direction: row;
        }

        .logo-container {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          @media screen and (orientation: landscape) {
            width: 100%;
          }
          @media screen and (min-width: $desktop) {
            width: 50%;
          }

          img {
            width: 75%
          }
        }

        .content-wrapper {
          h2 {
            margin: 0;
          }
          @media screen and (orientation: landscape) {
            width: 100%;
            display: none;
          }
          @media screen and (min-width: $desktop) {
            display: block;
            width: 50%;
            font-size: 2em;
          }

        }

        .divider {
          @include divider($yellow, 1px);
          width: 50%;
          margin: $padding / 2;
          @media screen and (orientation: landscape) {
            display: none;
          }
          @media screen and (min-width: $iphone7) {
            margin: $padding * 2;
          }
        }
      }
    }
  }


  /*--------------------------------------
                SECOND BLOCK
  ---------------------------------------*/
  .second-block {
    padding: $padding ($padding / 2);
    position: relative;
    background-color: $black;
    height: auto;
    min-height: calc(100vh - $menuHeight);
    @media screen and (min-width: $iphone7){
      padding: $padding;
    }
    @media screen and (min-width: $desktop) {
      display: flex;
      justify-content: center;
    }

    img {
      width: 50%;
      height: auto;
      max-width: 200px;
      margin: 0 auto;
      @media screen and (min-width: $desktop){
        max-width: none;
        margin: 0;
        height: 100%;
        width: 30%;
        position: relative;
      }
    }

    .content-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: $padding * 2;
      @media screen and (min-width: $desktop){
        width: 50%;
        justify-content: center;
        margin-left: $padding * 2;
      }
    }
  }


  /*--------------------------------------
                THIRD BLOCK
  ---------------------------------------*/
  .third-block {
    width: 100%;
    height: auto;
    min-height: calc(100vh - $menuHeight);

    .info-nav {
      width: 100%;
      font-family: $headerfont2;
      font-weight: 700;
      text-transform: uppercase;
      height: 50px;
      display: flex;

      .info-how, .info-what, .info-who {
        width: 33.333333%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }

      .info-how {
        background-color: $yellow;
        color: $black;
      }
      .info-what {
        background-color: $black;
        color: $white;
      }
      .info-who {
        background-color: $red;
        color: $white;
      }
    }

    .content-wrapper {
      width: 100%;
      position: relative;
      @media screen and (min-width: $desktop) {
        display: flex;
        height: calc(100vh - 110px);
     }

      .what-description {
        padding: $padding / 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $black;
        justify-content: center;

        @media screen and (min-width: $desktop) {
          width: 40%;
        }

        li {
          color: $yellow;
          font-weight: 700;
          span {
            color: $white;
            font-weight: 300;
            text-transform: none;
          }
        }

        span {
          color: $yellow;
          font-weight: 700;
          text-transform: uppercase;
          margin: ($padding / 2) 0;
        }
      }

      .how-description {
        padding: $padding / 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $yellow;
        color: $black;
        justify-content: center;

        .headerline {
          border-left-color: $red;
        }

        .button {
          color: $black;
          border-color: $black;
        }

        @media screen and (min-width: $desktop) {
          width: 40%;
        }
      }

      .who-description {
        padding: $padding / 2;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: $red;
        justify-content: center;

        @media screen and (min-width: $desktop) {
          width: 40%;
        }

        .headerline {
          color: $yellow;
          border-left-color: $white;
        }
      }

      .gallery-wrapper {
        width: 100%;
        height: 500px;
        position: relative;
        overflow: hidden;
        @media screen and (min-width: $desktop) {
          width: 60%;
          height: 100%;
        }

        .block-region-third-block {
          width: 100%;
          height: 100%;
        }

        .how-gallery {
          div {
            width: 100%;
            height: 100%;

            iframe {
              width: 100%;
              height: 100%;
            }
          }
        }

        .what-gallery, .who-gallery, .how-gallery {
          width: 100%;
          height: 100%;
        }
      }

    }
  }


  /*--------------------------------------
                FOURTH BLOCK
  ---------------------------------------*/
  .fourth-block {
    width: 100%;
    background-color: $red;
    display: flex;
    flex-direction: column;
    padding: $padding ($padding / 2);
    justify-content: center;
    align-items: center;
    @media screen and (min-width: $iphone7){
      padding: $padding;
    }

    @media screen and (min-width: $desktop){
      flex-direction: row;
      flex-wrap: wrap;

      h2 {
        width: 100%;
      }
    }

    .content-wrapper {
      width: 100%;
      order: 1;
      @media screen and (min-width: $desktop){
        order: 0;
        width: 50%;
        padding: 0 $padding;
      }
    }

    .contact {
      order: 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: $padding;
      @media screen and (min-width: $desktop){
        order: 1;
        width: 50%;
        align-self: flex-start;
      }

      p {
        font-size: $fontLarge;
        text-align: center;
        padding: 0;
        line-height: $fontLarge;
      }

      a {
        font-family: $headerfont2;
        font-weight: 700;
        font-size: $fontLarge * 1.5;
        letter-spacing: $spacing;
      }
    }
  }

}
