.button {
  border: 3px solid $yellow;
  color: $yellow;
  display: inline-flex;
  border-radius: 100px;
  padding: 0.5em 1em;
  margin: 1em auto;
  transition: $transnorm;
  font-family: $headerfont2;
  text-transform: uppercase;
  font-weight: 700;

  i {
    font-size: $fontMedium;
    margin-left: 0.5em;
    position: relative;
  }

  &:hover {
    color: darken($yellow, 15%);
    border-color: darken($yellow, 15%);

    i {
      animation: buttonArrowRight 2s infinite;
    }
  }
}

@keyframes buttonArrowRight {
  0% {
    left: -5px;
  }
  50% {
    left: 5px;
  }
  100% {
    left: -5px;
  }
}

.scroll-down {
  text-align: center;
  font-family: $headerfont2;
  text-transform: uppercase;
  font-weight: 700;

  i {
    position: relative;
    font-size: $fontMedium;
  }

  &:hover {
    cursor: pointer;

    i {
      animation: buttonArrowDown 2s infinite;
    }
  }
}

@keyframes buttonArrowDown {
  0% {
    top: 0;
  }
  50% {
    top: 10px;
  }
  100% {
    top: 0;
  }
}

.socializer {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  li {
    a {
      display: block;
      padding: 20px;
    }
  }
  &.no-email {
    .e-mail {
      display: none;
    }
  }
  &.centered {
    justify-content: center;
  }
  &.large {
    font-size: $fontMedium;
  }
}
