body {
  background-color: $black;
  color: $white
}

@media screen and (min-width: $desktop) {
  .columns-50 {
    width: 100%;
    display: flex;
    flex-wrap: wrap;

    .column {
      width: 50%;
    }
  }
}

.panel-1col {
  width: 100%;
  padding: $padding ($padding / 2);

  @media screen and (min-width: $desktop) {
    padding: $padding;
  }
}


#misc-page-wrapper {
  main {
    padding: $padding ($padding / 2);
  }
}
