.inventory-block {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  .views-row {
    width: 100%;
    padding: 5px;
    @media screen and (min-width: $desktop) {
      width: 25%;
    }

    .inventory-list {
      width: 100%;

      &:hover {
        .title-container {
          background-color: lighten($black, 5%);
          border-left: 3px solid $yellow;
          transition: $transnorm;
        }
        .image-container {
          .image-overlay {
            opacity: 1;
            transition: $transnorm;

            i {
              opacity: 1;
              transform: scale(1);
              transition: $transdelay;
            }
          }
        }
      }
      .image-container {
        width: 100%;
        position: relative;

        img {
          width: 100%;
          height: auto;
        }
        .image-overlay {
          background-color: $blackoverlay;
          color: $yellow;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: $fontLarge;
          text-shadow: $shadow;
          opacity: 0;
          i {
            opacity: 0;
            transform: scale(0);
          }
        }
      }
      .title-container {
        text-align: center;
        padding: 20px 30px;
        background-color: lighten($black, 10%);
        border-left: 3px solid $red;

        h3 {
          margin: 0;
          color: $white;
          font-size: 1em;
        }
      }
    }
  }
}


.inventory-gallery {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  .views-row {
    width: 50%;
    padding: 2px;

    &.featured {
      width: 100%;
    }
    
    .baguettebox {
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.baguettebox {
  position: relative;
  display: block;

  .image-overlay {
    background-color: $blackoverlay;
    color: $yellow;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $fontLarge;
    text-shadow: $shadow;
    opacity: 0;
    i {
      opacity: 0;
      transform: scale(0);
    }
  }

  &:hover {
    .image-overlay {
      opacity: 1;
      transition: $transnorm;

      i {
        opacity: 1;
        transform: scale(1);
        transition: $transdelay;
      }
    }
  }
}
