#main-footer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: $padding ($padding/2);
  z-index: 10;

  @media screen and (min-width: $desktop) {
    padding: $padding ($padding * 2);
  }

  p {
    padding: 0;
  }

  .footer-top {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media screen and (min-width: $desktop) {
      flex-direction: row;
      justify-content: space-between;
    }

    .to-the-top {
      color: $yellow;
      display: flex;
      transition: $transnorm;

      i {
        margin-left: 5px;
      }

      &:hover {
        color: darken($yellow, 15%);
        cursor: pointer;
      }
    }
  }

  .footer-mid {
    width: 100%;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: $desktop) {
      flex-direction: row;
      justify-content: space-between;
      margin-top: $padding;
      align-items: flex-end;
    }

    .copyright {
      order: 1;
      text-align: center;
      color: lighten($black, 40%);
      //width: 30%;

      @media screen and (min-width: $desktop) {
        text-align: left;
        order: 0;
      }
      a {
        display: block;
        margin: 20px 0;
      }
    }

    .company-info {
      order: 0;
      text-align: center;

      @media screen and (min-width: $desktop) {
        text-align: right;
        order: 1;
      }
    }
  }



}
.footer-bottom {
  width: 100%;
  font-size: $fontSmall / 1.5;
  text-align: center;
  color: lighten($black, 20%);
  margin-top: $padding;
  padding: $padding ($padding / 2);
  background-color: darken($black, 5%);
  text-transform: uppercase;
  letter-spacing: $spacing;
  z-index: 10;
}

#cookie-popup {
  width: 100%;
  height: auto;
  padding: 20px 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: darken($black, 10%);
  z-index: 200;
  font-size: $fontSmall;
  display: none;

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @media screen and (min-width: $desktop) {
      flex-direction: row;
    }
  }

  .button-small {
    font-family: $headerfont2;
    text-transform: uppercase;
    font-weight: 700;
    background-color: $yellow;
    color: $black;
    letter-spacing: $spacing;
    border-radius: 100px;
    padding: 5px 15px;
    margin: 10px 20px;
    transition: $transnorm;
    font-size: $fontSmall;
    text-align: center;
    line-height: $lineheight;

    &:hover {
      cursor: pointer;
      background-color: darken($yellow, 10%);
    }
  }
}
