form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;

  label {
    display: block;
    font-weight: 700;
    text-transform: uppercase;
  }

  .form-item {
    width: 100%;
    margin: ($padding / 4) 0;
  }

  input, textarea {
    font-family: $primaryfont;
    width: 100%;
    font-size: 1em;
    line-height: $lineheight;
    border: none;
    background-color: $white;
    padding: 10px;
    box-shadow: $shadowInset;
    border-radius: 7px;
    margin: 10px 0;
    color: $black;
  }

  select {
    background-color: transparent;
    color: $yellow;
    font-family: $primaryfont;
    font-size: 1em;
    border: 2px solid $yellow;
    padding: 10px;
    display: block;
  }

  .form-submit {
    width: auto;
    box-shadow: none;
    background-color: transparent;
    font-size: $fontLarge;
    border-width: 3px;
    @media screen and (min-width: $desktop){
      font-size: inherit;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
