#maintenance-wrapper {
  .content-wrapper {
    padding: $padding ($padding / 2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;

    .logo {
      width: 80%;
      margin-bottom: 50px;

      @media screen and (orientation: landscape) {
        width: 50%;
      }
      img {
        width: 100%;
        height: auto;
      }
    }

    h1 {
      color: $yellow;
    }

    p {
      text-align: center;
      margin: 20px 0;
    }

    .links {
      display: flex;
      font-size: $fontLarge;
      justify-content: center;
      align-items: center;
      a {
        margin: 20px;
        font-size: 2em;
      }
    }

    .phone {
      display: block;
      font-family: $headerfont2;
      font-size: 1.3em;
      font-weight: 700;
    }
  }
}
