#stock-item-layout {
  padding: $padding ($padding / 2);
  @media screen and (min-width: $desktop) {
    padding: ($padding * 2) $padding;
  }
  .column-layout {
    width: 100%;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
      display: flex;
      justify-content: space-between;
    }

    .column {
      width: 100%;
      padding: ($padding / 4) 0;
      @media screen and (min-width: $desktop) {
        width: 49%;
        padding: 0;
      }
    }
  }

  h1 {
    border-left: 3px solid $yellow;
    text-align: left;
    padding: 0 15px;
  }

  .field {
    .label {
      text-transform: uppercase;
      font-weight: 700;
    }
  }

  .block-region-column-right-info {
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .field {
      margin: 10px;
      @media screen and (orientation: landscape) {
        margin:  30px;
      }
      p {
        padding: 0;
        margin: 0;
      }
    }
  }

  .block-region-middle, .block-region-bottom {
    padding: ($padding / 4) ($padding / 2) ;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
    }
  }
}
