#main-navigation {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: $menuHeight;
  display: flex;
  align-items: flex-end;

  @media screen and (min-width: $desktop) {
    transition: $transnorm;

    &.past-point {
      background-color: darken($black, 5%);
      box-shadow: $shadowLight;
    }
  }


  #mobile-menu-button {
    width: 60px;
    height: 60px;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: $fontLarge;
    color: $white;
    background-color: darken($black, 5%);
    transition: $transnorm;
    z-index: 1;

    @media screen and (min-width: $desktop) {
      display: none;
    }

    &.selected {
      transition: $transnorm;

      color: $yellow;
    }
  }

  #main-menu {
    width: 100%;
    position: absolute;
    top: 0;
    background-color: $black;
    font-family: $headerfont2;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: $spacingMedium;
    padding: ($padding / 4) 0 $padding 0;
    font-size: $fontMedium;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media screen and (min-width: $desktop) {
      padding: $padding / 4;
      font-size: $fontSmall;
      background-color: transparent;
    }


    ul {
      list-style: none;
      width: 100%;

      @media screen and (min-width: $desktop) {
        display: flex;
      }

      li {
        width: 100%;
        text-align: center;

        a {
          display: inline-block;
          padding: ($padding / 4) $padding;
          margin: 0 auto;
          color: $white;

          @media screen and (min-width: $desktop) {
            padding: 10px 30px;

            &:hover {
              color: $yellow;
            }
          }

          &.is-active {
            border-bottom: 1px solid $yellow;
          }
          &.selected {
            border-bottom: 1px solid $yellow;
          }
        }
      }
    }

    .button {
      @media screen and (min-width: $desktop) {
        display: none;
      }
    }
  }
}
