#plain-page-layout {
  padding: $padding ($padding / 2);
  @media screen and (min-width: $desktop) {
    padding: ($padding * 2) $padding;
  }
  .fullwidth-top {
    width: 100%;
    min-height: 400px;
    overflow: hidden;
    position: relative;

    img {
      min-width: 100%;
      min-height: 100%;
      position: absolute;

      @supports (transform: translate(-50%, -50%)) or (-ms-transform: translate(-50%, -50%)) or (-webkit-transform: translate(-50%, -50%)) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  .block-region-middle, .block-region-bottom {
    padding: ($padding / 4) ($padding / 2) ;
    @media screen and (min-width: $iphone7) {
      padding: ($padding / 2) $padding;
    }
  }
}
